@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;1,300;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gowun+Dodum&display=swap);


.app {
    box-sizing: border-box;
}

.Header {
    font-family: 'Open Sans', sans-serif;
    font-weight: 500;
    text-align: center;
    font-size: 2vw;
    
}

.background {
    position: relative;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    padding-top: 2%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
}

